// build script hook - don't remove
(function ($) {
  $.fn.mfb = function (options) {
    "use strict";

    var animations = ["mfb-zoomin", "mfb-slidein", "mfb-slidein-spring", "mfb-fountain"];
    var placements = ["mfb-component--tl", "mfb-component--tr", "mfb-component--bl", "mfb-component--br"];

    /**
                                                                                                            * Some defaults
                                                                                                            */
    var self = this;
    var overlay = {
      element: $("<div class='mfb-component__overlay'></div>"),
      opened: false };


    options = options || {};
    options.buttons = options.buttons || [];
    options.activePrincipalButton = options.activePrincipalButton || false;
    options.placement = typeof options.placement !== "undefined" ? placements.indexOf(options.placement) !== -1 ? options.placement : "mfb-component--br" : "mfb-component--br";
    options.efect = typeof options.efect !== "undefined" ? animations.indexOf(options.efect) !== -1 ? options.efect : "mfb-slidein" : "mfb-slidein";
    options.clickHoverOpt = options.clickHoverOpt || "click";
    options.toggleMethod = options.toggleMethod || "data-mfb-toggle";
    options.menuState = options.menuState || "data-mfb-state";
    options.menuStateValue = options.menuStateValue || "closed";
    options.isOpen = options.isOpen || "open";
    options.isClosed = options.isClosed || "closed";
    options.mainButtonClass = options.mainButtonClass || "mfb-component__button--main";
    options.restingIcon = options.restingIcon || "mo-icon bz-plus";
    options.activeIcon = options.activeIcon || "mo-icon bz-cross";

    /**
                                                                    * @todo ask: I think this is useless cause we don't need to remove the main button, remove the complete component will be cleaner
                                                                    */
    // $("[" + options.toggleMethod + "='" + options.clickHoverOpt + "'] ." + options.mainButtonClass, this).remove();
    $(this).children(".mfb-component-holder").remove();
    /**
                                                         * @fix How about the events that was bind before?
                                                         */

    var tmpl =
    "<ul class=\"mfb-component-holder #: data.placement # #: data.efect #\" #: data.toggleMethod #=\"#: data.clickHoverOpt #\" #: data.menuState#=\"#: data.menuStateValue #\">" +
    "   <li class=\"mfb-component__wrap\">" +
    "      #if (data.activePrincipalButton && data.buttons.length >= 1){#" +
    "       <a #if(typeof data.buttons[0].label !== \"undefined\"){# data-mfb-label=\"#: data.buttons[0].label #\"#}# class=\"mfb-component__button--main\">" +
    "           <i class=\"mfb-component__main-icon--resting mfb-icon-font bz-#:data.restingIcon#\"></i><i class=\"mfb-component__main-icon--active mfb-icon-font bz-#:data.buttons[0].icon#\"></i>" +
    "       </a>" +
    "       #}else{#" +
    "       <a #if(typeof data.label !== \"undefined\"){# data-mfb-label=\"#: data.label #\"#}# class=\"mfb-component__button--main\">" +
    "           <i class=\"mfb-component__main-icon--resting mfb-icon-font bz-#:data.restingIcon#\"></i><i class=\"mfb-component__main-icon--active mfb-icon-font bz-#:data.activeIcon#\"></i>" +
    "       </a>" +
    "       #}#" +
    "       # if(data.buttons.length > 0){#" +
    "       <ul class=\"mfb-component__list\">" +
    "           #for(var i = (data.activePrincipalButton ? 1 : 0), len = data.buttons.length; i< len; i++){#" +
    "           <li data-id=\"#: data.buttons[i].id #\">" +
    "               <a href=\"#: data.buttons[i].link #\" data-index=\"#: i#\" #if(typeof data.buttons[i].label !== \"undefined\"){# data-mfb-label=\"#: data.buttons[i].label #\"#}# class=\"mfb-component__button--child\">" +
    "                  <i class=\"mfb-component__child-icon mfb-icon-font bz-#: data.buttons[i].icon #\"></i>" +
    "               </a>" +
    "           </li>" +
    "           #}#" +
    "       </ul>" +
    "       #}#" +
    "   </li>" +
    "</ul>";

    /**
              * Internal references
              */
    var target,
    currentState;

    /**
                   * For every menu we need to get the main button and attach the appropriate evt.
                   */
    function attachEvt(elems, evt, handler) {
      for (var i = 0, len = elems.length; i < len; i++) {
        elems[i].addEventListener(evt, handler, false);
      }
    }

    function configureBlur(element, blur) {
      if (blur) {
        element.siblings().addClass("mfb-component__overlay--blur");
        element.closest("[data-role]").siblings().addClass("mfb-component__overlay--blur");
      } else {
        element.siblings().removeClass("mfb-component__overlay--blur");
        element.closest("[data-role]").siblings().removeClass("mfb-component__overlay--blur");
      }
    }

    /**
       * The open/close action is performed by toggling an attribute
       * on the menu main element.
       *
       * First, check if the target is the menu itself. If it's a child
       * keep walking up the tree until we found the main element
       * where we can toggle the state.
       */
    function toggleButton(evt) {
      target = evt.target;
      while (target && !target.getAttribute(options.toggleMethod)) {
        target = target.parentNode;
        if (!target) {
          return;
        }
      }
      currentState = target.getAttribute(options.menuState) === options.isOpen ? options.isClosed : options.isOpen;

      if (options.activePrincipalButton && currentState !== "open") {
        var button = options.buttons[0];
        if (options.click) {
          options.click(evt, $.extend(button, { principal: true }), this);
        }
        configureBlur($(target), true);
      }

      if (currentState === "open" && !overlay.opened) {
        overlay.element.appendTo(target).bind("click", function () {
          this.remove();
          overlay.opened = false;
          target.setAttribute(options.menuState, "close");
          configureBlur($(target), false);
        });
        configureBlur($(target), true);
      } else {
        overlay.element.remove();
        configureBlur($(target), false);
      }

      overlay.opened = !overlay.opened;

      target.setAttribute(options.menuState, currentState);
    }

    function clickChildButton(evt) {
      var index = $(this).data("index");
      var button = options.buttons[index];

      $(".mfb-component__overlay", self).click();

      if (options.click) {
        evt.preventDefault();
        options.click(evt, $.extend(button, { principal: false }), this);
      }
    }

    function clickPrincipalButton(evt) {
      if (options.click) {
        evt.preventDefault();
        options.click(evt, { principal: true }, this);
      }
    }

    var template = kendo.template(tmpl, { useWithBlock: false });
    var html = template(options);

    if (options.position === "prepend") {
      this.prepend(html);
    } else {
      this.append(html);
    }

    // Principal button to toggle
    var toggle = options.buttons.length > 0 ? toggleButton : clickPrincipalButton;
    attachEvt($("[" + options.toggleMethod + "='" + options.clickHoverOpt + "'] ." + options.mainButtonClass, this), "click", toggle);

    // Secondary buttons to click
    attachEvt($(".mfb-component__button--child", this), "click", clickChildButton);
  };

  // build script hook - don't remove
})(jQuery);


(function ($) {
  $.fn.bz_navigation_buttons = function (options) {
    options = options || {};
    var MARGIN = 2;
    var buttons = options.buttons || [];
    var isDesign = options.isDesign || false;
    var buttonSelected = options.buttonSelected;
    var lastActionExecuted = options.lastActionExecuted || false;
    var allowAddButtons = options.allowAddButtons || false;
    var $element = this;


    var addButtonTemplate = '<div class="bz-button--add"><svg class="ico_16x16" ><use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#icon-plus"></use></svg></div>';
    var containerTemplate = '<ul class="bz-buttons--container"></ul>';
    var buttonTemplate = '<li class="bz-button--container" data-id="{1}"><button class="bz-btn">{0}</button></li>';
    var rightNavigationButton = '<div class="bz-button--rigth"><svg class="ico_16x16" ><use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#icon-arrow-rigth"></use></svg></div>';
    var leftNavigationButton = '<div class="bz-button--left"><svg class="ico_16x16" ><use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#icon-arrow-left"></use></svg></div>';

    $element.empty();
    $element.addClass('bz-buttons--navigation');

    var $container = $(containerTemplate);
    $container.appendTo($element);


    var processButtons = function processButtons() {
      var result = MARGIN;
      for (var index = 0; index < buttons.length; index++) {
        (function (button) {
          var caption = button.properties.caption;
          var $elementButton = $(buttonTemplate.replace("{0}", caption).replace("{1}", button.properties.id));
          var height = 0;

          var handlerOnClickButton = function handlerOnClickButton() {
            $container.trigger('onSelectButton', {
              $element: $elementButton,
              button: button });


            var targetElement = $container.get(0);
            var clientWidth = targetElement.clientWidth;
            var offset = isDesign ? 35 : 0;

            if (targetElement.scrollWidth > clientWidth) {
              if (height > clientWidth) {targetElement.scrollLeft = height - clientWidth + offset;} else
              {
                if (height - targetElement.scrollLeft < targetElement.scrollLeft) {targetElement.scrollLeft = height - (targetElement.scrollLeft + offset);}
              }
            }
          };

          $elementButton.
          on('click', handlerOnClickButton).
          appendTo($container);

          height = result = result + $elementButton.get(0).offsetWidth + MARGIN;

          if (isDesign && typeof buttonSelected == "string" && button.properties.id == buttonSelected) {
            setTimeout(handlerOnClickButton, 100);
          }

        })(buttons[index].button), result;
      };


      if (isDesign) {
        var $addButton = $(addButtonTemplate);

        if (allowAddButtons) {
          $addButton.click(function () {
            $container.trigger('onAddButton');
          });

          $addButton.appendTo($element);
        }

        var htmlButtons = $container.get(0);

        var heightAddButton = $addButton.get(0).scrollWidth;
        var heightWrapper = $element.get(0).scrollWidth;
        var heightButtons = htmlButtons.scrollWidth;


        var $rightNavigationButton = $(rightNavigationButton);
        var rightNavigationButtonIsLocated = heightButtons > heightWrapper - heightAddButton;

        if (rightNavigationButtonIsLocated) {$rightNavigationButton.insertAfter($container);}

        $rightNavigationButton.on("click", function () {
          htmlButtons.scrollLeft += 40;
        });

        var leftNavigationButtonIsLocated = false;
        var $leftNavigationButton = $(leftNavigationButton);


        $leftNavigationButton.on("click", function () {
          htmlButtons.scrollLeft -= 40;
        });

        $container.on("scroll", function (ev) {
          var targetElement = ev.target;
          var maxScrollLeft = targetElement.scrollWidth - targetElement.clientWidth;
          var scrollLeft = targetElement.scrollLeft;

          if (scrollLeft === 0 && leftNavigationButtonIsLocated) {
            leftNavigationButtonIsLocated = false;
            $leftNavigationButton.detach();
          }

          if (rightNavigationButtonIsLocated && scrollLeft === maxScrollLeft) {
            rightNavigationButtonIsLocated = false;
            $rightNavigationButton.detach();
          }

          if (!leftNavigationButtonIsLocated && scrollLeft > 0) {

            $leftNavigationButton.prependTo($element);
            leftNavigationButtonIsLocated = true;
          }

          if (!rightNavigationButtonIsLocated && scrollLeft < maxScrollLeft) {
            rightNavigationButtonIsLocated = true;
            $rightNavigationButton.insertAfter($container);
          }
        });

        if (lastActionExecuted === "addButton") {
          if (rightNavigationButtonIsLocated) {
            htmlButtons.scrollLeft = htmlButtons.scrollWidth - htmlButtons.clientWidth;
          }
        }
      }
    };

    processButtons();

    return $container;
  };

})(jQuery);